.footer_container {
    background-image: url("./../../assets/images/footer/WhatsApp Image 2023-06-16 at 18.10.44.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: center;
    background-position-x: right;
    position: relative;
    background-blend-mode: color-dodge;
    height: 330px;
}


.globe_image {
    background-image: linear-gradient(to right,
            rgba(0, 0, 0, 1) 20%,
            rgba(0, 0, 0, .9) 40%,
            rgba(0, 0, 0, .8) 60%,
            rgba(0, 0, 0, .7) 70%,
            rgba(0, 0, 0, .5) 75%,
            rgba(0, 0, 0, .0) 100%);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.all_links_below_mble {
    display: none;
}

@media only screen and (max-width:600px) {
    .footer_container {
        height: 550px;
    }

    .all_links_above_mble {
        display: none;
    }

    .all_links_below_mble {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 10px 0;
        row-gap: 5px;

    }

    .globe_image {
        background-image: linear-gradient(to right,
                rgba(0, 0, 0, 0.9) 20%,
                rgba(0, 0, 0, .8) 30%,
                rgba(0, 0, 0, .6) 53%,
                rgba(0, 0, 0, .4) 100%,
                rgba(0, 0, 0, .5) 73%,
                rgba(0, 0, 0, .0) 100%);
            }

}