.policies_type_options {
    display: flex;
    justify-content: center;
    margin: 50px 0;
}

.policies_row_card_container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    place-items: center;
    row-gap: 50px;
    column-gap: 20px;
    align-items: stretch;
}

.policies_card {
    border: 1px solid black;
    padding: 20px 60px;
    box-shadow: 2px 2px 5px black;
    border-radius: 20px;
    width: 300px;

}

.policies_btn_div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.policies_card_btn {
    border: 2px solid black;
    background-color: white;
    padding: 3px 15px;
    margin-top: 5px;
    border-radius: 20px;
}

.policies_card_btn:hover {
    background-color: black;
    border: 2px solid black;
    color: white;
}


.policies_selected_content {
    padding: 5px 70px;
    background-color: black;
    color: white;
    border-radius: 18px;
    border: 2px solid
}

.policies_unselected_content {
    padding: 5px 70px;
    background-color: white;
    color: black;
    border: 2px solid black;
    border-radius: 18px;
}
@media only screen and (max-width:600px){
    .policies_row_card_container {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        place-items: center;
        row-gap: 50px;
        column-gap: 20px;
    }
    .policies_type_options div{
        padding: 4px 27px;
        font-size: 13px;
    }
}
@media only screen and (max-width:834px) and (min-width:601px){
    .policies_row_card_container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        place-items: center;
        row-gap: 50px;
        column-gap: 20px;
    }
    .policies_type_options div{
        padding: 5px 30px;
        font-size: 15px;
    }
}

@media only screen and (max-width:1024px) and (min-width:835px){
    .policies_row_card_container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        place-items: center;
        row-gap: 50px;
        column-gap: 20px;
    }
}