.type_options {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.accordion {
  max-width: 768px;
  margin: 50px auto;
}

.accordion-item {
  list-style: none;
  border-radius: 30px;
  border: none;
  margin: 5px 0;
}

.accordion-toggle {

  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* background-color: RGB(244, 230, 210); */
  padding: 1em;
  border-radius: 30px;
  border: 2px solid black;
  font-weight: 700;
  /* position: relative; */
}

.accrdn_toggle_btn {
  font-size: 32px;
  font-weight: 700;
  line-height: 8px;
  display: flex;
  align-items: center;
}

