.accordion-content {
  background-color: #fdfdfc;
  padding: 1em;
  /* position: relative; */
  /* left: 50px; */
  /* top: 16px; */
  padding: 10px 20px;
  border-radius: 30px;
  margin: 10px 0;
  border: 2px solid black;
}

.accordion-toggle:hover {
  background-color: #FCF3E3;
}

.accordion-toggle h3 {
  margin: 0;
}


.selected_content_ven {
  padding: 5px 70px;
  background-color: black;
  color: white;
  border-radius: 18px;
  border: 2px solid
}

.unselected_content_ven {
  padding: 5px 70px;
  background-color: white;
  color: black;
  border: 2px solid black;
  border-radius: 18px;
}

.selected_content_cust {
  padding: 5px 70px;
  background-color: black;
  color: white;
  border-radius: 18px;
  border: 2px solid
}

.unselected_content_cust {
  padding: 5px 70px;
  background-color: white;
  color: black;
  border: 2px solid black;
  border-radius: 18px;
}
