.accordion::-webkit-scrollbar{
  width: 7px;
}
.accordion::-webkit-scrollbar-track{
  background: #f1f1f1; 
}
.accordion::-webkit-scrollbar-thumb{
  background: rgb(29, 28, 28); 
  border-radius: 20px;
}
.accordion::-webkit-scrollbar-thumb:hover{
  background: black; 
}

/* search bar start  */
.input_Wrapper{
  display: flex; 
  align-items: center;
  column-gap: 5px;
  background-color:rgb(248, 247, 247);
  padding: 5px 10px;
  width: 300px;
  border: 2px solid black;
  border-radius: 20px;
}
.input_Wrapper input{
  outline: none;
  width: 100%;
  background-color:rgb(248, 247, 247);


}

/* search bar end */

