.login_main_container{
    width: 100%;
    height: 100vh;
    background-color: bisque;
    display: flex;
    justify-content: center;
    align-items: center;
}
.login_inside_container{

    display: flex;
    justify-content: center;
    align-items: center;
    align-content: stretch;
    
}

/* input styling start */
.lbl_input{
    position: relative;
    width: 370px;
    margin-top: 20px;
}
.login_input{
    padding: 6px 13px;
    width: 100%;
    max-width: 100%;
    outline: none;
    border: 1px solid black;
    border-radius: 20px;
}
.login_input_placeholder{
    position: absolute;
    top: 5px;
    left: 15px;
    /* font-size: 14px; */
    transition: 0.3s;
    pointer-events: none;
}
.login_input:valid + .login_input_placeholder,
.login_input:focus + .login_input_placeholder{
    top: -12px;
    left: 15px;
    color: black;
    background-color: #fff;
}



/* input styling end */

.trading_btn_div {
    display: flex;
    justify-content: center;
    align-items: center;
}
.trading_card_btn {
    border: 2px solid black;
    background-color: white;
    padding: 3px 15px;
    margin-top: 5px;
    border-radius: 20px;
    width: 103px;
}

.trading_card_btn:hover {
    background-color: black;
    border: 2px solid black;
    color: white;
}