.policies_breadcum{
    width: 100%;
    height: 250px;
    background-image: url("./../../../assets/images/breadcum/Policies breadcrum.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.gradint_effect{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: linear-gradient(transparent, black);
}
.policies_bread_text{
    margin-top: 100px;
    z-index: 1;
 
}
.policies_bread_head{
   
    text-align: center; 
    font-size: 25px;
  font-weight: 700;
    
}
.policies_breadcum_link{
    padding: 2px 10px;
    border: 2px solid transparent;
}
.policies_breadcum_link:hover{
      /* background-color: bisque; */
      padding: 2px 10px;
      border: 2px solid #EFE0CA;
      color: black;
      /* font-weight: 600; */
      color: white;
      border-radius: 15px;
}